function sliderProducts() {
    var swiper = new Swiper('.products .mySwiper', {
        slidesPerView: 3,
        spaceBetween: 80,
        grid: {
            rows: 2,
        },
        navigation: {
            nextEl: ".products .swiper-button-next",
            prevEl: ".products .swiper-button-prev",
        },
        pagination: {
            el: ".products .swiper-pagination",
            type: "fraction",
        },
        breakpoints: {
            320: {
                slidesPerView: 2,
                spaceBetween: 20,
                grid: {
                    rows: 2,
                },
            },
            769: {
                slidesPerView: 2,
                spaceBetween: 80,
                grid: {
                    rows: 2,
                },
            },
            1024: {
                slidesPerView: 3,
                spaceBetween: 80,
                grid: {
                    rows: 2,
                },
            }
        }
    })
}

function sliderCases() {
    var swiper = new Swiper('.cases .mySwiper', {
        slidesPerView: 1,
        spaceBetween: 50,
        navigation: {
            nextEl: ".cases .swiper-button-next",
            prevEl: ".cases .swiper-button-prev",
        },
        pagination: {
            el: ".cases .swiper-pagination",
            type: "fraction",
        },
    })
}

function sliderTeam() {
    var swiper = new Swiper('.team .mySwiper', {
        slidesPerView: 4,
        spaceBetween: 36,
        navigation: {
            nextEl: ".team .swiper-button-next",
            prevEl: ".team .swiper-button-prev",
        },
        pagination: {
            el: ".team .swiper-pagination",
            type: "fraction",
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 16,
            },
            500: {
                slidesPerView: 2,
                spaceBetween: 16,
            },
            769: {
                slidesPerView: 3,
                spaceBetween: 16,
            },
            1024: {
                slidesPerView: 3,
                spaceBetween: 24,
            },
            1400: {
                slidesPerView: 4,
                spaceBetween: 36,
            }
        }
    })
}

$(document).ready(function () {

    sliderProducts();
    sliderCases();
    sliderTeam();


    $(".advantages__item-show").click((function () {
        $(this).parents('.advantages__item').toggleClass('advantages__item--active')
        $(this).toggleClass("advantages__item-show--active")
        $(this).siblings(".advantages__item-hidden").slideToggle()
    }));

    //Quiz
    $('.strategy__btn').click(function () {
        $(this).parents('.strategy').addClass('strategy--active')
        $('.strategy__inner').css('display','none')
        $('.quiz').addClass('quiz--active')
    })

    $('.quiz-next').click(function (e) {
        $('.quiz-prev').addClass('quiz-prev--active')

        let activeStep =$('.quiz-step--active').attr('data-step')

        if (activeStep === '1') {
            if($('.quiz-input:checked').length > 0) {
                $('.quiz-step1').find('.quiz-error').removeClass('quiz-error--active')
            } else{
                $('.quiz-step1').find('.quiz-error').addClass('quiz-error--active')
                $('.quiz-prev').removeClass('quiz-prev--active')
                return
            }
        }

        if (activeStep === '2') {
            if($('.quiz-input2:checked').length > 0) {
                $('.quiz-step2').find('.quiz-error').removeClass('quiz-error--active')
            } else{
                $('.quiz-step2').find('.quiz-error').addClass('quiz-error--active')
                return
            }
        }

        if (activeStep === '3') {
            if($('.quiz-input3:checked').length > 0) {
                $('.quiz-step3').find('.quiz-error').removeClass('quiz-error--active')
            } else{
                $('.quiz-step3').find('.quiz-error').addClass('quiz-error--active')
                return
            }
        }

        if (activeStep === '4') {
            if($('.quiz-input4:checked').length > 0) {
                $('.quiz-step4').find('.quiz-error').removeClass('quiz-error--active')
            } else{
                $('.quiz-step4').find('.quiz-error').addClass('quiz-error--active')
                return
            }
        }

        var $current = $('.quiz-step--active');
        var $current2 = $('.quiz-stage--active');

        $current.removeClass('quiz-step--active');
        $current.next('.quiz-step').addClass('quiz-step--active');
        if (!$current.next('.quiz-step').get(0)) {
            $('.quiz-body').remove()
            $('.quiz-btns').remove()
            $('.quiz-form').addClass('quiz-form--active')
        }

        $current2.removeClass('quiz-stage--active');
        $current2.next('.quiz-stage').addClass('quiz-stage--active');
    })

    $('.quiz-prev').click(function () {

        var $current = $('.quiz-step--active');
        var $current2 = $('.quiz-stage--active');

        $current.removeClass('quiz-step--active');
        $current.prev('.quiz-step').addClass('quiz-step--active');
        if ( $('.quiz-step').first().hasClass('quiz-step--active') ) {
            $(this).removeClass('quiz-prev--active')
        }

        $current2.removeClass('quiz-stage--active');
        $current2.prev('.quiz-stage').addClass('quiz-stage--active');
    })

    //Zoom
    function updateZoomVariable() {
        // Определяем ширину окна браузера
        const screenWidth = window.innerWidth;
        let zoomValue;

        if (screenWidth > 1920) {
            // Вычисляем zoom для экранов шире 1920px
            zoomValue = (screenWidth / 1920).toFixed(2);
        } else {
            // Для экранов 1920px или уже, устанавливаем zoom равным 1
            zoomValue = 1;
        }

        // Устанавливаем CSS переменную --zoom для корневого элемента (HTML)
        document.documentElement.style.setProperty('--zoom', zoomValue);
    }

// Вызываем функцию первоначально, чтобы установить переменную при загрузке страницы
    updateZoomVariable();

// Добавляем слушатель событий, чтобы обновлять переменную при изменении размера окна
    window.addEventListener('resize', updateZoomVariable);

})

// Animation
const animItems = document.querySelectorAll('._anim-items')

if (animItems.length > 0) {
    window.addEventListener('scroll', animOnScroll)
    function animOnScroll(params) {
        for (let index = 0; index < animItems.length; index++) {
            const animItem = animItems[index];
            const animItemHeight = animItem.offsetHeight;
            const animItemOffset = offset(animItem).top;
            const animStart = 4;

            let animItemPoint = window.innerHeight - animItemHeight / animStart;

            if (animItemHeight > window.innerHeight) {
                animItemPoint = window.innerHeight - window.innerHeight / animStart;
            }

            if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                animItem.classList.add('active');
            } else {
                if (!animItem.classList.contains('anim-no-hide')) {
                    animItem.classList.remove('active')
                }
            }
        }
    }
    function offset(el) {
        const rect = el.getBoundingClientRect(),
            scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
            scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
    }

    setTimeout(() => {
        animOnScroll();
    }, 300)
}


  